import './App.css';
import { useState, useEffect } from 'react';
import { ethers, utils } from 'ethers'

import Nav from "./../components/Nav";
// import About from "./../components/About";
// import Roadmap from "./../components/Roadmap";
import Rarity from "./../components/Rarity";
import FAQ from "./../components/FAQ";
// import Merch from "./../components/Merch";
import Team from "./../components/Team";
import Footer from "./../components/Footer";
import Countdown from "./../components/Countdown";
import Roadmap from "./../components/Roadmap";

import Button from 'react-bootstrap/Button';
import Dropdown from 'react-bootstrap/Dropdown';
import Modal from 'react-bootstrap/Modal';
import Toast from 'react-bootstrap/Toast';
import ToastContainer from 'react-bootstrap/ToastContainer';


import Contract from './../artifacts/contracts/CleverGirls.sol/CleverGirls.json';
const contractAddress = "0xc29b546b57BDa23A83e4537eB6096fab30A54eA5";

// PROTOS TESTING
// import Contract from './../artifacts/contracts/Protos.sol/Protos.json'
// const contractAddress = "0xE8894D58306C3c45742f4B502CC5caacF5ccd43B" // contract 6


function App() {
  const [price, setPrice] = useState(0.05);
  const [supply, setSupply] = useState(0);
  const [mintAmount, setMintAmount] = useState(1);
  const [network, setNetwork] = useState({name: "homestead"});
  const targetNetwork = "homestead";
  // const targetNetwork = "rinkeby";

  // MODAL
  const [show, setShow] = useState(false);
  const [modalTitle, setModalTitle] = useState("");
  const [modalMessage, setModalMessage] = useState("");

  // MINT CONTROL
  const [amountDisabled, setAmountDisabled] = useState(true);
  const [mintState, setMintState] = useState("sold");
  
  useEffect(() => {
    async function update() {
      console.log("update");
      const provider = new ethers.providers.Web3Provider(window.ethereum);
      const contract = new ethers.Contract(contractAddress, Contract.abi, provider)
      
      const newNetwork = await provider.getNetwork();  
      setNetwork(newNetwork);
      if(newNetwork.name !== targetNetwork) {
        return;
      }
      
      const supply = await contract.totalSupply();
      setSupply(supply.toString())
      if(supply.toString() === "5555") {
        setMintState("sold");
        return;
      }
      getPrice(); 
    }
    
    // checkNetwork();
    // init();
    // update();
    // setInterval(update, 4000);
  }, []);

  useEffect(() => {
    console.log("mintstate updated to:", mintState);
  }, [mintState]);


  async function init() {
    const provider = new ethers.providers.Web3Provider(window.ethereum);
    const contract = new ethers.Contract(contractAddress, Contract.abi, provider)
    const saleActive = await contract.saleActive();
    if (saleActive) {
      if (mintState === "disabled") { 
        setMintState("connect") 
      }
      return;
    }
    const presaleActive = await contract.presaleActive();
    if (presaleActive) {
      if (mintState === "disabled") { 
        setMintState("connect") 
      }
      return;
    }
    setMintState("disabled");
  }

  async function checkNetwork() {
    const provider = new ethers.providers.Web3Provider(window.ethereum);
    const network = await provider.getNetwork();
    if(network.name !== targetNetwork) {
      modalShow("Network Error", `You're on the ${network.name} network. Make sure you are connected to the Ethereum mainnet!`);
    }
    return network;
  }
  // MODAL
  function modalShow(title, message) {
    setModalTitle(title);
    setModalMessage(message);
    setShow(true);
  }
  
  function modalClose() {
    setShow(false);
  }

  async function getSupply() {
    const provider = new ethers.providers.Web3Provider(window.ethereum);
    const contract = new ethers.Contract(contractAddress, Contract.abi, provider)
    const supply = await contract.totalSupply();
    setSupply(supply.toString())
    if(supply === 0) {
      setMintState("sold");
    }
  }
  
  async function getPrice() {
    if (typeof window.ethereum !== 'undefined') {
      const provider = new ethers.providers.Web3Provider(window.ethereum);
      const contract = new ethers.Contract(contractAddress, Contract.abi, provider)
      const price = await contract.price();
      setPrice(utils.formatEther(price))
    }
  }

  // MINT AMOUNT DROPDOWN
  // const dropdown = [];
  const [dropdown, setDropdown] = useState([]);
  function setDropdownLimit(limit) {    
    let arr = [];
    for (let i = 1; i <= limit; i++) {
      arr.push(<Dropdown.Item key={i} onClick={()=>{setMintAmount(i)}}>{i}</Dropdown.Item>);
    }
    setDropdown(arr);
  }  


  // ------------------------------------------------------------------------------------------------
  // CONNECT
  // ------------------------------------------------------------------------------------------------
  async function connect() {
    const provider = new ethers.providers.Web3Provider(window.ethereum);
    const signer = provider.getSigner();
    const contract = new ethers.Contract(contractAddress, Contract.abi, signer)

    // NETWORK CHECK
    const network = await provider.getNetwork();
    if(network.name !== targetNetwork) {
      modalShow("Network Error", `You're on the ${network.name} network. Make sure you are connected to the Ethereum mainnet!`);
      return;
    }

    // ACCOUNT CONNECT
    const [acc] = await window.ethereum.request({ method: 'eth_requestAccounts' });
    // console.log("account", acc);

    // CHECK SALE ACTIVE
    const saleActive = await contract.saleActive();
    if (saleActive) {
      setDropdownLimit(5);
      setAmountDisabled(false);
      setMintState("sale");
      return;
    }

    // CHECK PRESALE ACTIVE
    const presaleActive = await contract.presaleActive();
    if (presaleActive) {

      // CHECK RESERVED
      let presaleReserved = await contract.presaleReserved(acc);
      presaleReserved = parseInt(presaleReserved.toString());
      if (presaleReserved > 0) {
        setDropdownLimit(presaleReserved);
        setAmountDisabled(false);
      } else {
        setAmountDisabled(true);
        modalShow("No reserved tokens", `Your address doesn't have any reserved Clever Girls for our presale.`)
      }
      setMintState("presale");
      return;
    }

    // DEFAULT ROUTE
    modalShow("Contract paused", `All sales are paused right now, check our discord for more information!`);
  }

  // ------------------------------------------------------------------------------------------------
  // PRESALE MINT
  // ------------------------------------------------------------------------------------------------
  async function presaleMint() {
    const provider = new ethers.providers.Web3Provider(window.ethereum);
    const signer = provider.getSigner();
    const contract = new ethers.Contract(contractAddress, Contract.abi, signer)

    // NETWORK CHECK
    const network = await provider.getNetwork();
    if(network.name !== targetNetwork) {
      modalShow("Network Error", `You're on the ${network.name} network. Make sure you are connected to the Ethereum mainnet!`);
      return;
    }
    
    // SALE ACTIVE CHECK
    const presaleActive = await contract.presaleActive();
    if (!presaleActive) {
      modalShow("Contract paused", `Presales are paused right now, check our discord for more information!`);
      return;
    }

    // CONNECTING WALLET
    const [acc] = await window.ethereum.request({ method: 'eth_requestAccounts' });

    // CHECK HOW MUCH RESERVED
    let presaleReserved = await contract.presaleReserved(acc);
    if (presaleReserved > 0) {
      if (presaleReserved > 3) {
        setAmountDisabled(false);
        setDropdownLimit(3);
      } else {
        setAmountDisabled(false);
        setDropdownLimit(presaleReserved);
        setMintAmount(1);
      }
    } else {
      setMintAmount(1);
      setAmountDisabled(true);
      modalShow("No reserved tokens", `Your address doesn't have any reserved Clever Girls for our presale.`)
      return;
    }

    // SANITIZE INPUT
    let mintAmountSanitized;
    if (mintAmount <= 0) {
      mintAmountSanitized = 1;
    } else if (mintAmount > 5) {
      mintAmountSanitized = 5;
    } else {
      mintAmountSanitized = mintAmount;
    }

    if (mintAmountSanitized > presaleReserved) {
      modalShow("Mint amount too high ", `You can not mint more than you have reserved for our presale.`)
      return;
    }
      
    const payment = (price * mintAmountSanitized).toFixed(2).toString();
    console.log("price:", payment)

    const gasEstimate = await contract.estimateGas.mintPresale(mintAmountSanitized, { value: ethers.utils.parseEther(payment) });
    console.log("est: ", gasEstimate.mul(1).toString());

    const gasLimit = parseInt(gasEstimate*1.3).toString()
    console.log("adj: ", gasLimit);

    await contract.mintPresale(mintAmountSanitized, { value: ethers.utils.parseEther(payment), gasLimit: gasLimit });

    const newMintAmount = presaleReserved - mintAmount;
    if (newMintAmount <= 0) {
      setDropdownLimit(1);
      setAmountDisabled(true);
      return;
    } else {
      setDropdownLimit(newMintAmount);
    } 
  }

  // ------------------------------------------------------------------------------------------------
  // STANDARD MINT
  // ------------------------------------------------------------------------------------------------
  async function saleMint() {
    const provider = new ethers.providers.Web3Provider(window.ethereum);
    const signer = provider.getSigner();
    const contract = new ethers.Contract(contractAddress, Contract.abi, signer)

    // NETWORK CHECK
    const network = await provider.getNetwork();
    if(network.name !== targetNetwork) {
      modalShow("Network Error", `You're on the ${network.name} network. Make sure you are connected to the Ethereum mainnet!`);
      return;
    }
    
    // SALE ACTIVE CHECK
    const saleActive = await contract.saleActive();
    if (!saleActive) {
      modalShow("Contract paused", `Sales are paused right now, check our discord for more information!`);
      return;
    }

    // CONNECTING WALLET
    const [acc] = await window.ethereum.request({ method: 'eth_requestAccounts' });
    console.log(acc);

    // SANITIZE INPUT
    let mintAmountSanitized;
    if (mintAmount <= 0) {
      mintAmountSanitized = 1;
    } else if (mintAmount > 5) {
      mintAmountSanitized = 5;
    } else {
      mintAmountSanitized = Math.round(mintAmount);
    }

    
    const payment = (price * mintAmountSanitized).toFixed(2).toString();
    console.log("mint price:", payment)

    const gasEstimate = await contract.estimateGas.mintToken(mintAmountSanitized, { value: ethers.utils.parseEther(payment) });
    console.log("est gasLimit: ", gasEstimate.toString());

    const gasLimit = parseInt(gasEstimate*1.3).toString()
    console.log("adj gasLimit: ", gasLimit);

    await contract.mintToken(mintAmountSanitized, { value: ethers.utils.parseEther(payment), gasLimit: gasLimit });


    // const payment = (price * mintAmountSanitized).toFixed(2).toString();
    // console.log("price:", payment)
    // await contract.mintToken(mintAmountSanitized, { value: ethers.utils.parseEther(payment) });
  }
  
  
  function MintButton() {
    if(mintState === "disabled") {
      return <div className="button-container disabled"><span className="mas">MINT!</span><Button className="mint-btn" disabled={true} size="lg">MINT!</Button></div>;
    } else if(mintState === "connect") {
      return <div className="button-container"><span className="mas">CONNECT!</span><Button className="mint-btn" size="lg" onClick={connect}>CONNECT!</Button></div>;
    } else if (mintState === "presale") {
      return <div className="button-container"><span className="mas">MINT PRESALE!</span><Button className="mint-btn" size="lg" onClick={presaleMint}>MINT PRESALE!</Button></div>;
    } else if (mintState === "sale") {
      return <div className="button-container"><span className="mas">MINT!</span><Button className="mint-btn" size="lg" onClick={saleMint}>MINT!</Button></div>;
    } else if (mintState === "sold") {
      return <div className="button-container disabled"><span className="mas">SOLD OUT!</span><Button disabled={true} className="mint-btn" size="lg">SOLD OUT!</Button></div>;
    } 
  }

  return (
    <div className="App">
      <ToastContainer id="toast" className="p-3" position="bottom-center">
          <Toast show={(network.name !== targetNetwork)}>
            <Toast.Header closeButton={false}>
              <strong className="me-auto">Network error</strong>
            </Toast.Header>
            <Toast.Body>Please connect to the Ethereum mainnet.</Toast.Body>
          </Toast>
        </ToastContainer>

      <Modal show={show} onHide={modalClose}>
        <Modal.Header closeButton>
          <Modal.Title>{modalTitle}</Modal.Title>
        </Modal.Header>
        <Modal.Body>{modalMessage}</Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={modalClose}>Close</Button>
        </Modal.Footer>
      </Modal>

      <Nav />
      <section id="teaser" className="videoBackground">
        <div className="vignette"></div>
        <div className="overlay"></div>
        <video id="video-full" playsInline="playsInline" autoPlay="autoPlay" muted="muted" loop="loop">
          <source src="videos/bg.webm" type="video/mp4"/>
        </video>
        <video id="video-mobile" playsInline="playsInline" autoPlay="autoPlay" muted="muted" loop="loop">
          <source src="videos/bg-mobile.webm" type="video/mp4"/>
        </video>
        
        <div className="container teaser-content">
          <div className="row">
            <h2 className="hatching-title">Hatching day</h2>
            <Countdown time="Sep 5, 2021 17:55:55 GMT+0200"/>
            <div className="col-12 col-lg-12 mint-box">
              <div className="row mint-row no-gutters">
                <div className="col-12 col-md-12">
                  <MintButton/>                  
                  <Dropdown>
                    <Dropdown.Toggle disabled={amountDisabled} id="mint-amount">{mintAmount}</Dropdown.Toggle>
                    <Dropdown.Menu> { dropdown } </Dropdown.Menu>
                  </Dropdown>
                </div>
              </div>
              <div className="row">
                <div className="col-lg-12 hero-minted"><span className="mint-data">{(price * mintAmount).toFixed(2)} ETH</span><span className="mint-data">{/*5555 - supply*/}0 left!</span><span className="mint-data">5 / tx</span></div>
              </div>              
            </div>
          </div>
        </div>
      </section>
      
      <Rarity/>
      <FAQ/>
      <Roadmap/>
      <Team/>
      <Footer/>
    </div>
  );
}
    
export default App;