import './style.css';
import { useState, useEffect } from 'react';

function Countdown(props) {
  const [days, setDays] = useState("");
  const [hours, setHours] = useState("");
  const [minutes, setMinutes] = useState("");
  const [seconds, setSeconds] = useState("");

  function zeroPad (s, i) {
    return String(s).padStart(i, '0');
  }
  // Run this only on componentMount
  useEffect(() => {
    // Set the date we're counting down to
    var countDownDate = new Date(props.time).getTime();

    // Update the count down every 1 second
    var y = setInterval(function() {
      // Get today's date and time
      var now = new Date().getTime();
      
      // Find the distance between now and the count down date
      var distance = countDownDate - now;
      
      // Time calculations for days, hours, minutes and seconds
      var _days = Math.floor(distance / (1000 * 60 * 60 * 24));
      var _hours = Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
      var _minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
      var _seconds = Math.floor((distance % (1000 * 60)) / 1000);
      
      // Display the result in the element with id="demo"
      setDays(zeroPad(_days, 2))
      setHours(zeroPad(_hours, 2))
      setMinutes(zeroPad(_minutes, 2))
      setSeconds(zeroPad(_seconds, 2))
      
      // If the count down is finished, write some text
      if (distance < 0) {
        setDays(0)
        setHours(0)
        setMinutes(0)
        setSeconds(0)
        clearInterval(y);
      }
    }, 1000);
  }, []);

  return (
    <div className="row d-flex justify-content-center align-items-center no-gutters countdown">
      <div className="col-2 countdown-number"><span className="number">{days}</span><br/><span className="label">DAYS</span></div>
      <div className="col-2 countdown-number"><span className="number">{hours}</span><br/><span className="label">HRS</span></div>
      <div className="col-2 countdown-number"><span className="number">{minutes}</span><br/><span className="label">MIN</span></div>
      <div className="col-2 countdown-number"><span className="number">{seconds}</span><br/><span className="label">SEC</span></div>
    </div>
  );
}
export default Countdown;

